/* Modal Styles - Custom Modal Structure */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}

.modal-overlay.open {
  opacity: 1;
  visibility: visible;
}

/* Modal Container */
.modal-container {
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  width: 95%;
  max-width: 1200px;
  max-height: 90vh;
  overflow: hidden;
  transform: scale(0.9) translateY(20px);
  transition: all 0.3s ease;
  position: relative;
}

.modal-overlay.open .modal-container {
  transform: scale(1) translateY(0);
}

/* Modal Header */
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid #E6D7A2;
  background: #D4AF5F;;
  color: #5F646D ;
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
  color: #5F646D ;
  order: 1;
}

.close-btn {
  background: none;
  border: none;
  color: #5F646D;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 8px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  order: 2;
}

.close-btn:hover {
  background-color: rgba(95, 100, 109, 0.1);
  color: #5F646D;
}

/* RTL Support */
[dir="rtl"] .modal-header {
  text-align: right;
  flex-direction: row-reverse;
}

[dir="rtl"] .modal-header .modal-title {
  order: 2;
}

[dir="rtl"] .modal-header .close-btn {
  order: 1;
}

[dir="rtl"] .modal-title {
  font-family: 'IBM Plex Sans Arabic', sans-serif !important;
}

[dir="ltr"] .modal-title {
  font-family: 'Poppins', sans-serif !important;
}

/* Modal Body */
.modal-body {
  padding: 2rem;
  max-height: 70vh;
  overflow-y: auto;
}

/* Form Sections */
.form-section {
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: #f8f9fa;
  border-radius: 8px;
  border: 1px solid #e9ecef;
}

.section-title {
  color: #5F646D;
  font-weight: 600;
  margin-bottom: 1.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #E6D7A2;
}

/* Form Controls */
.form-label {
  font-weight: 500;
  color: #5F646D;
  margin-bottom: 0.5rem;
}

.form-control,
.form-select {
  border: 1px solid #dee2e6;
  border-radius: 6px;
  padding: 0.75rem;
  font-size: 0.875rem;
  transition: all 0.2s ease;
}

.form-control:focus,
.form-select:focus {
  border-color: #BFAB86;
  box-shadow: 0 0 0 0.2rem rgba(212, 170, 58, 0.25);
}

.form-control.is-invalid,
.form-select.is-invalid {
  border-color: #dc3545;
  box-shadow: 0 0 0 0.2rem rgba(220, 53, 69, 0.25);
}

.invalid-feedback {
  font-size: 0.75rem;
  color: #dc3545;
  margin-top: 0.25rem;
}

/* Participants Section */
.participants-container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.participant-card {
  background: white;
  border: 1px solid #dee2e6;
  border-radius: 8px;
  padding: 1.5rem;
  position: relative;
  transition: all 0.2s ease;
}

.participant-card:hover {
  border-color: #d4ab3ab9;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.1);
}

.participant-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #e9ecef;
}

.participant-number {
  color: #495057;
  font-weight: 600;
  margin: 0;
  font-size: 1rem;
}

.participant-header .btn {
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
}

/* Form Grid */
.row {
  margin-left: -0.75rem;
  margin-right: -0.75rem;
}

.col-md-6 {
  padding-left: 0.75rem;
  padding-right: 0.75rem;
}

/* Buttons */
.btn {
  border-radius: 6px;
  font-weight: 500;
  transition: all 0.2s ease;
}

.btn-primary {
  background: #BFAB86;
  border: #BFAB86;
  color: #FFFFFF;
}

.btn-primary:hover {
  background: #B8941F;
  border: #B8941F;
  color: #FFFFFF;
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(212, 170, 58, 0.3);
}

.btn-outline-primary {
  border-color: #BFAB86;
  color: #BFAB86;
  background: transparent;
}

.btn-outline-primary:hover {
  background: #BFAB86;
  border-color: #BFAB86;
  color: #FFFFFF;
}

.btn-outline-danger {
  border-color: #C62828;
  color: #C62828;
  background: transparent;
}

.btn-outline-danger:hover {
  background: #C62828;
  border-color: #C62828;
  color: #FFFFFF;
}

.btn-outline-danger:disabled {
  border-color: #6c757d;
  color: #6c757d;
  opacity: 0.6;
}

/* Modal Footer */
.modal-footer {
  background: #f8f9fa;
  border-top: 1px solid #E6D7A2;
  padding: 1.5rem;
  border-radius: 0 0 12px 12px;
}

.modal-footer .btn {
  padding: 0.75rem 1.5rem;
  font-weight: 500;
}

/* Loading States */
.spinner-border-sm {
  width: 1rem;
  height: 1rem;
}

/* Error Display */
.alert {
  border-radius: 8px;
  border: none;
  padding: 1rem;
  margin-bottom: 1.5rem;
}

.alert-danger {
  background: #f8d7da;
  color: #721c24;
  border-left: 4px solid #dc3545;
}

.alert svg {
  color: #dc3545;
}

/* RTL Dropdown Arrow Alignment */
[dir="rtl"] .form-select {
  background-position: left 0.75rem center;
  padding-left: 2.25rem;
  padding-right: 0.75rem;
}

[dir="ltr"] .form-select {
  background-position: right 0.75rem center;
  padding-right: 2.25rem;
  padding-left: 0.75rem;
}

/* Ensure dropdown arrows don't overlap with text */
[dir="rtl"] .form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: 16px 12px;
}

[dir="ltr"] .form-select {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='m2 5 6 6 6-6'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-size: 16px 12px;
}

/* Focus state for RTL dropdowns */
[dir="rtl"] .form-select:focus {
  background-position: left 0.75rem center;
}

[dir="ltr"] .form-select:focus {
  background-position: right 0.75rem center;
}

/* Invalid state for RTL dropdowns */
[dir="rtl"] .form-select.is-invalid {
  background-position: left 0.75rem center;
}

[dir="ltr"] .form-select.is-invalid {
  background-position: right 0.75rem center;
}

